<script>

    export default {

        name: 'historys',
        props: ["historys"],
         data(){
             return{
                 totalProblemList:[],
                 showList:[],
                 dataCount:3,   /*几页*/
                 pageSize:4,    /*每页几条*/
             };
         },
         mounted() {
             this.$http('/historys', 'post').then((res) => {

                 this.totalProblemList = res.data;
                 this.totalProblemList = this.historys.supports;
                 console.log(this.totalProblemList);
                 this.dataCount = this.totalProblemList.length;
                 if (this.dataCount < this.pageSize) {
                     this.showList = this.totalProblemList;
                 } else {
                     this.showList = this.totalProblemList.slice(0, this.pageSize)
                 }
         })
         },
        methods: {
            open4() {
                this.$notify.error({
                    title: 'Error',
                    message: 'This is an error message'
                });
            },
            changepage(index){
                let start = (index - 1)*this.pageSize;
                let end = index * this.pageSize;
                this.showList = this.totalProblemList.slice(start,end)
                console.log(this.showList)
            }
        }
    }
</script>
